@import url(https://fonts.googleapis.com/css?family=Open+Sans);
*{box-sizing:border-box}html{font-family:"Open Sans", sans-serif;color:#fff5f5}body,h1,ul,section,menu,switch{margin:0;padding:0;border:0}.app{display:flex;flex-direction:column;text-align:center;padding:30px 60px;min-height:100vh;background-color:#181a1e}.app__title{text-align:center;margin-bottom:24px;color:#fff}

.main{font-size:1.5em;margin-bottom:25px}.main__link{color:inherit;text-decoration:none;transition:color 0.3s, text-shadow 0.3s}.main__link:hover{text-shadow:0 6px 15px #ffffff80;cursor:pointer}.main__link--active{color:#ff3232}

.tab__content{text-align:justify}.tab__content--warning{color:#cfcfcf;font-style:italic;letter-spacing:0.1rem}

.tabs{margin:15px auto;width:640px}.tabs__menu{margin-bottom:24px}.tabs__list{display:flex;justify-content:center;text-align:center}.tabs__item{position:relative;padding:15px 25px;margin-left:15px;top:0;width:150px;float:left;color:inherit;text-decoration:none;letter-spacing:1px;background-color:#181a1e;border:1px #ffffff26 solid;box-shadow:0 3px 10px #ffffff1a;transition:box-shadow 0.5s ease 0s, top 0.5s ease 0s, border 0.5s ease 0s, color 0.5s ease 0s}.tabs__item:first-child{margin-left:0}.tabs__item:hover{box-shadow:0 6px 15px #ffffff80;top:-3px;border:1px #ffffff4d solid;color:#fffc;cursor:pointer}.tabs__item--active{box-shadow:0 6px 15px #ff1e1e80;border:1px #ff1e1e66 solid;top:-3px;color:#ff3232}.tabs__item--active:hover{border-bottom:5px #ff2121 solid;box-shadow:0 6px 15px #ff1e1e80;border:1px #ff1e1e66 solid;top:-3px;color:#ff3232}

